.navbar{
    display: flex;
    justify-content: space-between;
    // padding: 1rem 3rem;
    align-items: center;
    max-width: 1200px;
    margin: 40px auto;
    
    &__links{
        display: flex;
        margin-right: 10rem;
        
    }
    &__link{
        text-decoration: none;
        color: crimson;
        &:not(:last-child){
            margin-right: 2rem;
        }
    }
}
.logo{
    font-size: 2rem;
    font-weight: 600;
    color: rgb(0, 174, 255);
}