*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  max-width: 1200px;
  margin: 0 auto;
  text-align: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.blog-preview{
  width: 50%;
  padding:2rem 3rem;
  text-align: left;
  border-radius: 10px;
  cursor: pointer;
}
.blog-preview a{
  text-decoration: none;
  color: gray;
}
.blog__heading{
  color: crimson;
  font-size: 2rem;
  margin-bottom: 1rem;
}
.blog-preview:hover{
  box-shadow: 0 1rem 2rem rgba(29, 29, 29, .2);

}
article h3{
  color: crimson;
  margin-bottom: 1rem;
}
article p{
  color: rgb(34, 34, 34);
  margin-bottom: 1rem;
}
form{
  display: flex;
  flex-direction: column;
  max-width: 330px;
  justify-self: center;
  margin: 0 auto;
}
form *{
  margin-bottom: 1rem;
}
form input{
  height: 30px;
}
form select{
  height: 30px;
  font-size: 1rem;
}

form button{
  height: 50px;
  background-color: crimson;
  margin: 0 auto;
  min-width: 60px;
  border: none;
  color: white;
  padding: 0 10px;
  border-radius: 10px;
  margin-bottom: 2rem;
}